<template>
    <div class="row">
        <grid-vue class="input-field" tamanho="12">
            <textarea v-model="conteudo" class="materialize-textarea"></textarea>
            <label>O que está aconntecendo?</label>
        </grid-vue>
        
        <p v-if="conteudo" >
          <grid-vue tamanho="2" offset="10" class="btn waves-effect waves-dark">Publicar</grid-vue>
        </p>
    </div>
</template>

<script>
import GridVue from '@/components/layouts/GridVue'

export default {
  name: 'PublicarConteudoVue',
  components:{
    GridVue
  },
  data(){
      return{
        conteudo:''
      }
  }
}
</script>

<style scoped>

</style>



